
.carousel{
  width: 80%;
  margin: 5rem;
  margin-top: 0;
  margin-bottom: 0;
  justify-content: center;
  align-items: center;
  margin-left: 9rem;
}

.carousel img {
  height: 500px;
  border-radius: 2rem;
  justify-content: center;
}
main aside{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 88vh;
}

main middle{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.text{
  text-align: center;
  margin-left: 45%;
}
.btn {
display: block;
}
p.active{
  text-align: center;
  display: none;
}
h1.active{
  text-align: center;
  display: none;
}

.tellobee{
  margin-left: 63%;
}

.error{
  color: red;
}

.word__day{
 padding: 1.5rem;
  margin-top: 2.2rem;
  margin-bottom: 1.2rem;
  height: 200px;
  width: 100%;
  border-radius: 2rem 2rem;
  background: #a82050;
}

.word__day h2{
  text-align: center;
  color: antiquewhite;
  margin-top: 0;
  margin-bottom: 1.7rem;
  }

.quote p{
color: #f0d6d5;
text-align: center;
text-decoration: solid;
font-size: larger;
margin-top: 1.2rem;
}

@media screen and (max-width: 1024px){
.text{
  margin: 0;
  margin-left: 0;
}
.tellobee{
  margin: 0;
}
.carousel{
  margin-left: 5rem;
}
}
